@use "../util" as *;

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: rem(23);

  .card__subtitle {
    white-space: nowrap;
  }

  .card__count {
    margin-bottom: 0;
  }

  img,
  .card__change {
    justify-self: end;
  }

  .card__count,
  .card__change {
    align-self: flex-end;
  }
}
