html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--fontFamilyInter);
  background-color: var(--background);
  color: var(--textColor);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

img {
  display: block;
}

:root {
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagramEnd: hsl(37, 97%, 70%);
  --instagramMiddle: hsl(5, 77%, 71%);
  --instagramStart: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggleBgEnd: hsl(210, 78%, 56%);
  --toggleBgStart: hsl(146, 68%, 55%);
  --toggleTextLight: hsl(230, 19%, 60%);
  --toggleLightBg: hsl(230, 22%, 74%);
  --darkBg: hsl(230, 17%, 14%);
  --darkTopBg: hsl(232, 19%, 15%);
  --darkCardBg: hsl(228, 28%, 20%);
  --darkDesaturetedBlueText: hsl(228, 34%, 66%);
  --darkWhite: hsl(0, 0%, 100%);
  --darkCardHover: hsl(228, 25%, 27%);
  --lightBg: hsl(0, 0%, 100%);
  --lightTopBg: hsl(225, 100%, 98%);
  --lightCardBg: hsl(227, 47%, 96%);
  --lightGrayishBlueText: hsl(228, 12%, 44%);
  --lightDarkBlue: hsl(230, 17%, 14%);
  --lightCardHover: hsl(228, 33%, 91%);
  --background: var(--lightBg);
  --textColor: var(--lightDarkBlue);
  --headerTextColor: var(--lightGrayishBlueText);
  --cardBgColor: var(--lightCardBg);
  --cardTextColor: var(--lightGrayishBlueText);
  --hoverCardBg: var(--lightCardHover);
  --countTextColor: var(--lightDarkBlue);
  --toggle: var(--toggleTextLight);
  --toggleBg: var(--toggleLightBg);
  --toggleButton: var(--lightBg);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--darkBg);
    --textColor: var(--darkWhite);
    --headerTextColor: var(--darkDesaturetedBlueText);
    --cardBgColor: var(--darkCardBg);
    --cardTextColor: var(--darkDesaturetedBlueText);
    --hoverCardBg: var(--darkCardHover);
    --countTextColor: var(--darkWhite);
    --toggle: var(--textColor);
    --toggleBg: linear-gradient(
      225deg,
      var(--toggleBgStart) 0%,
      var(--toggleBgEnd) 98.02%
    );
    --toggleButton: var(--darkBg);
  }
}
:root {
  --fontFamilyInter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card__layout {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .card__layout {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .card__layout {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  background-color: var(--cardBgColor);
  color: var(--cardTextColor);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  overflow: hidden;
  transition: background-color 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background-color: var(--hoverCardBg);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.3125rem;
  background-image: linear-gradient(225deg, var(--instagramStart) 0, var(--instagramMiddle) 50.91%, var(--instagramEnd) 100%);
  top: 0;
  left: 0;
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-weight: 700;
  font-size: 0.75rem;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--countTextColor);
  letter-spacing: -0.125rem;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  color: var(--cardTextColor);
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limeGreen);
}
.card__change--down {
  color: var(--brightRed);
}
.card__number {
  margin-left: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__subtitle {
  white-space: nowrap;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: flex-end;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  cursor: pointer;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
  width: 1.25rem;
  height: 1.5rem;
  margin: 0 -0.0003125rem 0 -0.0003125rem;
  cursor: pointer;
}
.toggle__background {
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggleBg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggleButton);
  transition: all 0.15s ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
  right: 0.1875rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--headerTextColor);
}